
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap');

.head {
    width: 100%;
    height: calc(100vh - 3.5rem);
    display:flex;
    justify-content: center;
    align-items: center;
}

.head::before {
    content: "";
    width: 100%;
    height: calc(100vh - 3.5rem);
    background-image: url("../images/header.png");
    background-size: cover;
    position: absolute;
    z-index: -1;
}
.cont {
    width: 800px;
    height: 286px;
    text-align: center;
}

.contH1 {
    color: #fff; 
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 42px;
    line-height: 129.9%;
}

.contBtn {

    margin-top: 2em;
    font-weight: 600;
    padding: .5em .8em;
    background: #FFFFFF;
    border-radius: 5px;
    border: none
}

@media screen and (max-width:480px) {
    .head{
        overflow: hidden;
    }
    .head::before {
        background-image: url("../images/headImage.png");
      
    }
    .cont {
        width: 100%;
        height: 200px;
        /* border: 2px solid red; */
    }
    
    .contH1 {
        font-size: 21px;
    
    }
    
    .contBtn {
    
        margin-top: 2em;
        font-weight: 600;
        padding: .5em .8em;
        background: #FFFFFF;
        border-radius: 5px;
        border: none
    }
    
}